@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300&display=swap');

@layer base{
  body{
    @apply font-Montserrat;
  }
  li{
    @apply px-4;
    @apply cursor-pointer;
    @apply items-center;
    @apply m-2;
  }
}